@import './index.css';

/* src/components/Summary.css */
.summary-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: var(--surface-color);
    padding: 20px;
    border-radius: 8px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
    max-width: 400px;
    margin: 20px auto;
}

.summary-title {
    font-size: 2rem;
    margin-bottom: 20px;
    color: var(--text-primary-color);
}

.summary-item {
    font-size: 1.2rem;
    margin: 10px 0;
    color: var(--text-secondary-color);
}

.summary-button {
    background-color: var(--success-color);
    color: var(--on-success-color);
    padding: 10px 20px;
}

.balance {
    font-weight: bold;
    color: var(--success-color); /* Verde para saldo positivo */
}

@media (max-width: 600px) {
    .summary-container {
        padding: 15px;
    }

    .summary-title {
        font-size: 1.5rem;
    }

    .summary-item {
        font-size: 1rem;
    }
}