@import './index.css';

.dashboard-container {
    display: flex;
    min-height: 100vh;
    background: var(--background, #f5f6fa);
}

.dashboard-sidebar {
    width: 220px;
    background: var(--primary, #2d3a4b);
    display: flex;
    flex-direction: column;
    padding: 2rem 1rem;
    box-shadow: 2px 0 8px rgba(0,0,0,0.04);
    transition: width 0.2s;
}

.dashboard-tab {
    background: none;
    border: none;
    color: var(--on-primary, #fff);
    font-size: 1.1rem;
    padding: 1rem;
    margin-bottom: 1rem;
    text-align: left;
    border-radius: 6px;
    cursor: pointer;
    transition: background 0.2s;
}

.dashboard-tab.active,
.dashboard-tab:hover {
    background: var(--secondary, #4e6fae);
    color: var(--on-secondary, #fff);
}

.dashboard-main {
    flex: 1;
    padding: 2rem;
    background: var(--background, #f5f6fa);
    overflow-y: auto;
    min-width: 0;
}

/* Responsividade */
@media (max-width: 900px) {
    .dashboard-container {
        flex-direction: column;
    }
    .dashboard-sidebar {
        flex-direction: row;
        width: 100%;
        padding: 0.5rem 0.5rem;
        box-shadow: 0 2px 8px rgba(0,0,0,0.04);
        justify-content: center;
        align-items: center;
    }
    .dashboard-tab {
        margin: 0 0.5rem 0 0.5rem;
        padding: 0.7rem 1rem;
        font-size: 1rem;
        text-align: center;
    }
    .dashboard-main {
        padding: 1rem 0.5rem;
    }
}

@media (max-width: 600px) {
    .dashboard-sidebar {
        flex-direction: row;
        width: 100%;
        padding: 0.3rem 0.2rem;
    }
    .dashboard-tab {
        font-size: 0.95rem;
        padding: 0.5rem 0.7rem;
    }
    .dashboard-main {
        padding: 0.5rem 0.1rem;
    }
}