/* src/components/TransactionForm.css */

.transaction-form {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: #ffffff;
    padding: 20px;
    border-radius: 8px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
    max-width: 400px;
    margin: 20px auto;
}

.form-title {
    font-size: 1.8rem;
    margin-bottom: 20px;
    color: #333;
}

.form-input, .form-textarea {
    padding: 10px;
    margin: 10px 0;
    border: 1px solid #ccc;
    border-radius: 4px;
    font-size: 1rem;
    width: 100%;
}

.transaction-form select.form-input {
    padding: 10px;
    margin: 10px 0;
    border: 1px solid #ccc;
    border-radius: 4px;
    font-size: 1rem;
    width: 100%;
}

.form-textarea {
    resize: vertical;
    min-height: 80px;
}

.form-button {
    padding: 10px;
    margin: 10px 0;
    border: none;
    border-radius: 4px;
    background-color: #28a745;
    color: white;
    font-size: 1rem;
    cursor: pointer;
    transition: background-color 0.3s;
}

.form-button:hover {
    background-color: #218838;
}

@media (max-width: 600px) {
    .transaction-form {
        padding: 15px;
    }

    .form-title {
        font-size: 1.5rem;
    }

    .form-input, .form-textarea, .form-button {
        font-size: 0.9rem;
    }
}
