/* src/components/Auth.css */

.auth-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;
    background-color: #f4f4f4;
    padding: 20px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
    border-radius: 8px;
}

.auth-title {
    font-size: 2rem;
    margin-bottom: 20px;
    color: #333;
}

.auth-form {
    display: flex;
    flex-direction: column;
    width: 100%;
    max-width: 400px;
}

.auth-input {
    padding: 10px;
    margin: 10px 0;
    border: 1px solid #ccc;
    border-radius: 4px;
    font-size: 1rem;
}

.auth-button {
    padding: 10px;
    margin: 10px 0;
    border: none;
    border-radius: 4px;
    background-color: #007bff;
    color: white;
    font-size: 1rem;
    cursor: pointer;
    transition: background-color 0.3s;
}

.auth-button:hover {
    background-color: #0056b3;
}

@media (max-width: 600px) {
    .auth-container {
        padding: 10px;
    }

    .auth-title {
        font-size: 1.5rem;
    }

    .auth-input {
        font-size: 0.9rem;
    }

    .auth-button {
        font-size: 0.9rem;
    }
}
