/* src/styles/TransactionList.css */
@import './index.css';

.transaction-list-container {
    background-color: var(--background-card);
    padding: 24px;
    border-radius: 12px;
    box-shadow: var(--shadow);
    max-width: 800px;
    margin: 30px auto;
    font-family: 'Inter', sans-serif; /* Using a modern font, assuming it's available or imported elsewhere */
}

.list-title {
    font-size: 1.8rem;
    font-weight: 600;
    margin-bottom: 20px;
    color: var(--text-main);
    text-align: left;
    border-bottom: 1px solid var(--border);
    padding-bottom: 10px;
}

.transaction-list {
    list-style-type: none;
    padding: 0;
}

.transaction-item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 16px 8px;
    border-bottom: 1px solid var(--border);
    transition: background-color 0.3s ease;
}

.transaction-item:last-child {
    border-bottom: none;
}

.transaction-item:hover {
    background-color: var(--background-card-alt);
}

.transaction-details {
    display: flex;
    align-items: center;
    flex: 2;
    flex-wrap: wrap; /* Allow details to wrap on small screens */
}

.transaction-icon {
    margin-right: 16px;
    /* Add styles for icons if you have them */
}

.transaction-title {
    font-weight: 500;
    color: var(--text-main);
    margin-right: 16px; /* Add some space between title and category */
}

.transaction-category {
    color: var(--text-muted);
    margin-left: 16px;
    background-color: var(--primary-light);
    color: var(--primary);
    padding: 4px 8px;
    border-radius: 6px;
    font-size: 0.8rem;
    font-weight: 500;
    white-space: nowrap; /* Prevent category from breaking into multiple lines */
}

.transaction-info {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    flex: 1;
}

.transaction-amount {
    font-weight: 600;
    color: var(--success);
    min-width: 100px;
    text-align: right;
}

.transaction-amount.expense {
    color: var(--danger);
}

.transaction-date {
    color: var(--text-muted);
    font-size: 0.9rem;
    margin-left: 24px;
    min-width: 80px;
    text-align: right;
    white-space: nowrap;
}

/* Responsive Styles */
@media (max-width: 768px) {
    .transaction-list-container {
        padding: 20px;
        margin: 20px;
    }

    .list-title {
        font-size: 1.6rem;
    }

    .transaction-item {
        flex-direction: column;
        align-items: flex-start;
        padding: 12px 4px;
    }
    
    .transaction-details {
        width: 100%;
        margin-bottom: 10px;
    }

    .transaction-info {
        width: 100%;
        justify-content: space-between;
        margin-top: 10px;
    }

    .transaction-date {
        margin-left: 16px;
    }

    .transaction-category {
        margin-left: 0;
        margin-top: 8px;
    }
}

@media (max-width: 480px) {
    .transaction-list-container {
        padding: 15px;
        margin: 10px;
        border-radius: 8px;
    }

    .list-title {
        font-size: 1.4rem;
    }

    .transaction-details {
        flex-direction: column;
        align-items: flex-start;
    }

    .transaction-title {
        margin-right: 0;
        margin-bottom: 8px;
    }
    
    .transaction-category {
        margin-left: 0;
    }

    .transaction-info {
        flex-direction: column;
        align-items: flex-end;
    }

    .transaction-amount {
        margin-bottom: 8px;
    }

    .transaction-date {
        font-size: 0.8rem;
        margin-left: 0;
    }
}
